
@font-face {
    font-family: 'Roboto Mono Light';
    src: url('./RobotoMono-Light.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'Roboto Mono Regular';
    src: url('./RobotoMono-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'Roboto Mono Medium';
    src: url('./RobotoMono-Medium.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

.table-line:nth-child(odd) {
    background-color: #f0f0f0; 
}

.table-line:nth-child(even) {
    background-color: #e0e0e0;
}
  
.monospace-font {
    font-family: 'Roboto Mono Regular', monospace;
}

.highlight {
    font-family: 'Roboto Mono Medium', monospace;
    color: #167ce1;
}

.light-font {
    font-family: 'Roboto Mono Light', monospace;
}

.highlight-notam {
    font-family: 'Roboto Mono Medium', monospace;
    color: #167ce1;
}

.timestamp {
    font-size: 0.9em;
}

.small-title {
    font-family: 'Roboto Mono Medium', monospace;
    font-size: 1.4em;
    margin: 8px 0px;
}